<template>
  <div class="grid-img-box" :class="{show:!isShowImg,hide:isShowImg}">
    <viewer :images="imgData">
      <div class="grid-img-item" v-for="(i,index) in imgData" :key="index">
        <img :src="imageSrc + i + '.jpg'" alt />
        <!-- <span
          v-text="i.title"
          class="title"
        ></span>
        <span v-text="i.user"></span>-->
      </div>
    </viewer>
    <div class="maskDiv" v-if="isShowImg">
      <Spin size="large" fix v-if="isShowImg"></Spin>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: this.$route.query.id,
      classId: this.$route.query.classId,
      imageSrc: "",
      imgData: [],
      isShowImg: true,
    };
  },
  created() {},
  mounted() {
    this.baseData.photoBookModule.photoBookData.allBookData.forEach(
      (item, index) => {
        if (item.classPhotoBookId == this.classId) {
          item.bookData.forEach((element) => {
            if (element.photoBookId == this.id) {
              this.imageSrc = element.imageSrc;
              this.imgData = element.data;
            }
          });
        }
      }
    );
    setTimeout(() => {
      this.init();
    }, 2000);
    setTimeout(() => {
      this.isShowImg = false;
    }, 3000);
  },
  methods: {
    init() {
      $(".grid-img-box").masonry({
        columnWidth: 20,
        itemSelector: ".grid-img-item", // 要布局的网格元素
        gutter: 20, // 网格间水平方向边距，垂直方向边距使用css的margin-bottom设置
        percentPosition: false, // 使用columnWidth对应元素的百分比尺寸
        stamp: ".grid-stamp", // 网格中的固定元素，不会因重新布局改变位置，移动元素填充到固定元素下方
        fitWidth: true, // 设置网格容器宽度等于网格宽度，这样配合css的auto margin实现居中显示
        originLeft: true, // 默认true网格左对齐，设为false变为右对齐
        originTop: true, // 默认true网格对齐顶部，设为false对齐底部
        containerStyle: { position: "relative" }, // 设置容器样式
        transitionDuration: "0.8s", // 改变位置或变为显示后，重布局变换的持续时间，时间格式为css的时间格式
        stagger: "0.03s", // 重布局时网格并不是一起变换的，排在后面的网格比前一个延迟开始，该项设置延迟时间
        resize: true, // 改变窗口大小将不会影响布局
        initLayout: true,
      });
    },
  },
};
</script>

<style lang="scss" src="@/assets/scss/photo_details.scss" scoped>
</style>


